import React, { useState, useContext } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import VideoSchema from '../../../model/VideoSchema';

import ApiContext from '../../../context/ApiContext';
import VideoRow from './VideoRow';
import VideoEditRow from './VideoEditRow';

const VideoTable = styled.table`
  border: none;

  tr, td, th {
    border: none;
  }

  th {
    font-weight: bold;
  }

  tr:nth-child(even) {
    background-color: #eee;
  }

  td:last-child {
    width: 50px;
  }
`;

const NO_EDIT = -1;
const initEditState = {
  quality: VideoSchema.properties.quality.enum[0],
  videoType: VideoSchema.properties.videoType.enum[0],
  url: '',
};

export default function VideoEditor(props) {
  const {
    uuid,
  } = props;

  const [editId, setEditId] = useState(NO_EDIT);
  const [editData, setEditData] = useState(initEditState);
  const [newData, setNewData] = useState(initEditState);

  const api = useContext(ApiContext);
  const qc = useQueryClient();

  const collectionQuery = useQuery(['videos', uuid], () => api.media.videos.fetchAll(uuid));
  const addQuery = useMutation((data) => { api.media.videos.create(uuid, data); });
  const updateQuery = useMutation(({ id, data }) => { api.media.videos.update(uuid, id, data); });
  const removeQuery = useMutation((id) => { api.media.videos.remove(uuid, id); });

  const openEditor = (id, data) => {
    setEditId(id);
    setEditData(data);
  };

  const closeEditor = () => {
    setEditData(initEditState);
    setEditId(NO_EDIT);
  };

  const doAdd = () => {
    addQuery.mutate(newData, {
      onSuccess: () => {
        qc.invalidateQueries(['videos', uuid]);
        setNewData(initEditState);
      },
    });
  };

  const doUpdate = () => {
    updateQuery.mutate({ editId, editData }, {
      onSuccess: () => {
        qc.invalidateQueries(['videos', uuid]);
        closeEditor();
      },
    });
  };

  const doRemove = (id) => {
    removeQuery.mutate(id, {
      onSuccess: () => {
        qc.invalidateQueries(['videos', uuid]);
      },
    });
  };

  const rows = collectionQuery.data?.map((video) => {
    if (video.id === editId) {
      return (
        <VideoEditRow
          data={editData}
          onChange={setEditData}
          onSave={doUpdate}
          onCancel={closeEditor}
          key={video.id}
        />
      );
    }
    return (
      <VideoRow
        key={video.id}
        id={video.id}
        quality={video.quality}
        videoType={video.videoType}
        url={video.url}
        onEdit={openEditor}
        onDelete={doRemove}
      />
    );
  });

  return (
    <VideoTable>
      <tbody>
        <tr>
          <th>quality</th>
          <th>video type</th>
          <th>url</th>
          <th>actions</th>
        </tr>

        {rows}

        <VideoEditRow
          data={newData}
          onChange={setNewData}
          onSave={doAdd}
        />
      </tbody>
    </VideoTable>
  );
}

VideoEditor.propTypes = {
  uuid: PropTypes.string.isRequired,
};
