import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Tag from './Tag';

const TagContainerBox = styled.ul`
  list-style-type: none;
  padding: 0;
`;

export default function TagEditor(props) {
  const { uuid, queries } = props;
  const [input, setInput] = useState('');

  const qc = useQueryClient();

  const collectionQuery = useQuery(['tags', uuid], () => queries.fetchAll(uuid), {
    enabled: !!uuid,
  });
  const addQuery = useMutation((name) => { queries.add(uuid, name); });
  const removeQuery = useMutation((name) => { queries.remove(uuid, name); });

  const doAdd = (name) => {
    addQuery.mutate(name, {
      onSuccess: () => {
        qc.invalidateQueries(['tags', uuid]);
        setInput('');
      },
    });
  };

  const doRemove = (name) => {
    removeQuery.mutate(name, {
      onSuccess: () => {
        qc.invalidateQueries(['tags', uuid]);
      },
    });
  };

  const tags = collectionQuery.data?.map((name) => (
    <Tag key={name} name={name} onDelete={doRemove} />
  ));

  return (
    <TagContainerBox>
      <h3>Tags</h3>
      <input
        type="text"
        name="tagname"
        value={input}
        onChange={(e) => { setInput(e.target.value); }}
      />
      <button type="button" onClick={() => { doAdd(input); }}>Add</button>
      <ul>{tags}</ul>
    </TagContainerBox>
  );
}

TagEditor.propTypes = {
  uuid: PropTypes.string.isRequired,
  queries: PropTypes.shape({
    fetchAll: PropTypes.func.isRequired,
    add: PropTypes.func.isRequired,
    remove: PropTypes.func.isRequired,
  }).isRequired,
};
