import SeriesSchema from '../model/SeriesSchema';
import EpisodeSchema from '../model/EpisodeSchema';
import { validate, validatePartial } from '../util/SchemaTools';
import ContentTagApi from './ContentTagApi';

export default function SeriesApi(axios) {
  const fetchAll = async function fetchAll() {
    const res = await axios.get('/series');
    return res.data._embedded?.media;
  };

  const create = async function create(series) {
    validate(series, SeriesSchema);
    return axios.post('/series', series);
  };

  const fetch = async function fetch(uuid) {
    const res = await axios.get(`/series/${uuid}`);
    return res.data;
  };

  const update = async function update(uuid, params) {
    validatePartial(params, SeriesSchema);
    return axios.put(`/series/${uuid}`, params);
  };

  const remove = async function remove(uuid) {
    return axios.delete(`/series/${uuid}`);
  };

  const fetchEpisodes = async function fetchEpisodes(uuid) {
    const res = await axios.get(`/series/${uuid}/episodes`);
    return res.data._embedded?.episodes;
  };

  const createEpisode = async function createEpisode(uuid, episode) {
    validate(episode, EpisodeSchema);
    return axios.post(`/series/${uuid}/episodes`, episode);
  };

  return {
    fetchAll,
    create,
    fetch,
    update,
    remove,
    episodes: {
      fetchAll: fetchEpisodes,
      create: createEpisode,
    },
    tags: ContentTagApi(axios, '/series'),
  };
}
