import Axios from 'axios';
import FormData from 'form-data';
import SeriesApi from './SeriesApi';
import MediaApi from './MediaApi';

let token = '';
let u = '';
let p = '';

export default function Api(baseURL) {
  const axios = Axios.create({
    baseURL,
  });

  axios.interceptors.request.use((config) => {
    if (['put', 'patch'].includes(config.method.toLowerCase())) {
      /* eslint no-param-reassign:0 */
      config.headers['X-HTTP-Method-Override'] = config.method.toUpperCase();
      config.method = 'post';
    }

    if (config.method.toLowerCase() === 'post') {
      if (config.data.thumbnail) {
        const fd = new FormData();

        Object.keys(config.data).forEach((key) => {
          fd.append(key, config.data[key]);
        });

        config.data = fd;
        config.headers['Content-Type'] = 'multipart/form-data';
      }
    }

    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }

    return config;
  });

  const login = async function login(username = u, password = p) {
    this.logout();
    const response = await axios.post('/login', { username, password });

    if (response.status !== 200) throw response.data;

    token = response.data.token;
    u = username;
    p = password;
  };

  const isLoggedIn = function isLoggedIn() {
    return token !== '';
  };

  const logout = function logout() {
    token = '';
  };

  const fetchAllTags = async function fetchAllTags() {
    const res = await axios.get('/tags');
    return res.data._embedded.tags.map(({ name }) => name);
  };

  return {
    login,
    logout,
    isLoggedIn,
    getUsername: () => u,
    series: SeriesApi(axios),
    media: MediaApi(axios),
    tags: { fetchAll: fetchAllTags },
  };
}
