import React from 'react';
import PropTypes from 'prop-types';

import VideoSchema from '../../../model/VideoSchema';

export default function VideoRow(props) {
  const {
    id,
    quality,
    videoType,
    url,
    onEdit,
    onDelete,
  } = props;

  return (
    <tr>
      <td>{quality}</td>
      <td>{videoType}</td>
      <td>{url}</td>
      <td>
        <button type="button" onClick={() => { onEdit(id, { quality, videoType, url }); }}>edit</button>
        <button type="button" onClick={() => { onDelete(id); }}>delete</button>
      </td>
    </tr>
  );
}

VideoRow.propTypes = {
  id: PropTypes.number.isRequired,
  quality: PropTypes.oneOf(
    VideoSchema.properties.quality.enum,
  ).isRequired,
  videoType: PropTypes.oneOf(
    VideoSchema.properties.videoType.enum,
  ).isRequired,
  url: PropTypes.string.isRequired,
  onEdit: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
};
