import BaseModel from './BaseModel';

const StreamableBaseSchema = {
  properties: {
    ...BaseModel.properties,

    duration: {
      type: 'integer',
      exclusiveMinimum: 0,
    },

    language: {
      type: 'string',
      allowEmpty: false,
      maxLength: 5,
    },
  },

  required: ['duration', 'language'],
};

export default StreamableBaseSchema;
